.hidden-input {
	color: transparent;
	background-color:transparent;
  border: none;
  outline: none;
	position: absolute;
	caret-color: transparent;
	bottom: 18px;
	right: 80px;
	z-index: -50;
}

.scrabble-tiles {
	text-align: left;
	left: 15px;
	position: relative;
	bottom: -1px;
}

.scrabble-input-box .scrabble-tiles.blinker:last-child:after {
	content: "";
	position: absolute;
	background-color: #335;
	margin-left: 5px;
	top: 7px;
	width: 3px;
	height: 46px;
	-webkit-animation: blinker 1s linear infinite;
	        animation: blinker 1s linear infinite;
}

.scrabble-input-box.large .scrabble-tiles.blinker:last-child:after {
	top: 74px;
}

.mobile .scrabble-tiles.blinker:last-child:after {
	margin-left: -10px;
	top: 2px;
	width: 2px;
	height: 40px;
}

.mobile .scrabble-input-box.large .scrabble-tiles.blinker:last-child:after {
	margin-left: -10px;
	top: 54px;
	width: 2px;
	height: 40px;
}


@-webkit-keyframes blinker {  
  0% { opacity: 1; }
  49% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 0; }
}


@keyframes blinker {  
  0% { opacity: 1; }
  49% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 0; }
}

